export default "import { NgModule } from \"@angular/core\";\nimport { RouterModule } from \"@angular/router\";\nimport { DEMO_PATH_TOKEN, NuiDocsModule, NuiLayoutModule, NuiMessageModule, SrlcStage } from \"@nova-ui/bits\";\n\nimport {\n    LayoutBasicExampleComponent,\n    LayoutComplexExampleComponent,\n    LayoutExampleComponent,\n    LayoutFitContentComponent,\n    LayoutInitSizeExampleComponent,\n    LayoutPageContentExampleComponent,\n    LayoutResizeExampleComponent,\n    LayoutSeparateSheetsExampleComponent,\n    LayoutTestComponent,\n    LayoutVisualTestComponent,\n} from \"./index\";\n\nconst routes = [\n    {\n        path: \"\",\n        component: LayoutExampleComponent,\n        data: {\n            \"srlc\": {\n                \"stage\": SrlcStage.ga,\n            },\n            showThemeSwitcher: true,\n        },\n    },\n    {\n        path: \"layout-test\",\n        component: LayoutTestComponent,\n        data: {\n            \"srlc\": {\n                \"hideIndicator\": true,\n            },\n        },\n    },\n    {\n        path: \"layout-visual-test\",\n        component: LayoutVisualTestComponent,\n        data: {\n            \"srlc\": {\n                \"hideIndicator\": true,\n            },\n        },\n    },\n];\n\n@NgModule({\n    imports: [\n        NuiLayoutModule,\n        NuiDocsModule,\n        NuiMessageModule,\n        RouterModule.forChild(routes),\n    ],\n    declarations: [\n        LayoutExampleComponent,\n        LayoutBasicExampleComponent,\n        LayoutSeparateSheetsExampleComponent,\n        LayoutFitContentComponent,\n        LayoutResizeExampleComponent,\n        LayoutTestComponent,\n        LayoutVisualTestComponent,\n        LayoutComplexExampleComponent,\n        LayoutInitSizeExampleComponent,\n        LayoutPageContentExampleComponent,\n    ],\n    providers: [\n        {\n            provide: DEMO_PATH_TOKEN,\n            useFactory: () => (<any>require).context(`!!raw-loader!./`, true, /.*\\.(ts|html|less)$/),\n        },\n    ],\n    exports: [\n        RouterModule,\n    ],\n})\nexport class LayoutModule {\n}\n";